import React, { useContext, useEffect, useState } from "react";
import {
  View,
  Text,
  StyleSheet,
  ActivityIndicator,
  Pressable,
} from "react-native";
import { API_BASE_URL, UI } from "../../config/constants";
import Test from "../../types/test";
import { Colors } from "@config/Colors";
import AnswerButton from "@components/Test/AnswerButton";
import TestProgressDisplay from "@components/Test/TestProgressDisplay";
import TestEndedPopUp from "@components/Test/TestEndedPopUp";
// import { StackScreenProps } from "@react-navigation/stack";
// import { RootStackParamList } from "@navigation/types";
// import { AuthContext } from "../contexts/AuthContext";

// type Props = StackScreenProps<RootStackParamList, "Test">;

const TestScreen: React.FC<{ token: string }> = props => {
  const { token } = props;
  const [isLoading, setIsLoading] = useState(true);
  const [isShowingAnswer, setIsShowingAnswer] = useState(false);
  const [test, setTest] = useState<Test | null>(null);
  const [isTestingAllQuestions, setIsTestingAllQuestions] = useState(false);
  const [currentQuestionIndex, setCurrentQuestionIndex] = useState(0);
  const [currentChoice, setCurrentChoice] = useState<string | null>(null);
  const [correctAnswerCount, setCorrectAnswersCount] = useState(0);
  const [wrongAnswerCount, setWrongAnswersCount] = useState(0);

  useEffect(() => {
    async function fetchNewTest() {
      try {
        const response = await fetch(`${API_BASE_URL}/tests`, {
          method: isTestingAllQuestions ? "GET" : "POST",
          headers: {
            Authorization: token,
          },
        });
        const data = await response.json();
        console.log(data.questions[0]);
        const testData: any = {};
        testData.id = data.id;
        testData.questions = data.questions.map((question: any) => ({
          id: question.id,
          body: question.content,
          answers: question.answers.map((answer: any) => ({
            id: answer.id,
            body: answer.content,
            isCorrect: answer.correct,
          })),
        }));
        setTest(testData);
        setIsLoading(false);
      } catch (error) {
        console.error(error);
      }
    }

    if (!test) {
      fetchNewTest();
    }
    console.log(isTestingAllQuestions);
  }, [token, test, isTestingAllQuestions]);

  const handleAnswerPress = (id: string) => {
    setCurrentChoice(id);
    setIsShowingAnswer(true);
    if (
      test?.questions[currentQuestionIndex].answers.find(a => a.id === id)
        ?.isCorrect
    ) {
      setCorrectAnswersCount(currentValue => currentValue + 1);
    } else {
      setWrongAnswersCount(currentValue => currentValue + 1);
    }
  };

  const handleNextQuestion = () => {
    setCurrentChoice(null);
    setCurrentQuestionIndex(currentValue => currentValue + 1);
    setIsShowingAnswer(false);
  };

  const handleStartNewTest = () => {
    setIsLoading(true);
    setIsShowingAnswer(false);
    setTest(null);
    setCurrentQuestionIndex(0);
    setCurrentChoice(null);
    setCorrectAnswersCount(0);
    setWrongAnswersCount(0);
  };

  const handleTestTypeToggle = () => {
    setIsTestingAllQuestions(currentValue => !currentValue);
    handleStartNewTest();
  };

  if (isLoading) {
    return (
      <View style={styles.centered}>
        <ActivityIndicator size="large" color={Colors.secondary} />
      </View>
    );
  }

  if (!test && !isLoading) {
    return (
      <View style={styles.screen}>
        <View style={styles.centered}>
          <Text>Error! No test...</Text>
        </View>
      </View>
    );
  }

  if (wrongAnswerCount >= 5 || currentQuestionIndex >= 20) {
    return (
      <TestEndedPopUp
        wrongAnswers={wrongAnswerCount}
        correctAnswers={correctAnswerCount}
        startNewTest={handleStartNewTest}
      />
    );
  }

  return (
    <View style={styles.screen}>
      <View style={styles.content}>
        <View style={styles.headBar}>
          <Pressable style={styles.button} onPress={handleTestTypeToggle}>
            <Text style={styles.buttonText}>Parcurge toate intrebarile</Text>
          </Pressable>
          <TestProgressDisplay
            correct={correctAnswerCount}
            wrong={currentQuestionIndex - correctAnswerCount}
            remaining={test!.questions.length - currentQuestionIndex}
          />
        </View>
        <View style={styles.questionContainer}>
          <View style={styles.questionCard}>
            <Text>
              {currentQuestionIndex + 1}
              {". "}
              {test!.questions[currentQuestionIndex].body}
            </Text>
          </View>
        </View>
        <View style={styles.answersContainer}>
          {test!.questions[currentQuestionIndex].answers.map(
            (answer, index) => (
              <AnswerButton
                id={answer.id}
                key={answer.id}
                body={answer.body}
                isCorrect={answer.isCorrect}
                index={index}
                isHighlighted={
                  currentChoice === answer.id ||
                  (answer.isCorrect && !!currentChoice)
                }
                onPress={handleAnswerPress}
              />
            ),
          )}
        </View>
      </View>
      {isShowingAnswer && (
        <Pressable style={styles.overlay} onPress={handleNextQuestion} />
      )}
    </View>
  );
};

const styles = StyleSheet.create({
  screen: {
    flex: 1,
    width: "100%",
    // justifyContent: "center",
    alignItems: "center",
    backgroundColor: Colors.background,
  },
  content: {
    padding: UI.padding,
  },
  centered: {
    flex: 1,
    justifyContent: "center",
    alignItems: "center",
  },
  headBar: {
    width: "100%",
    position: "relative",
    top: 0,
    flexDirection: "row",
    justifyContent: "space-between",
    alignItems: "flex-end",
  },
  questionContainer: {
    justifyContent: "center",
    alignItems: "center",
    width: "100%",
  },
  questionCard: {
    width: "100%",
    borderRadius: UI.borderRadius,
    padding: UI.padding,
    backgroundColor: Colors.secondary,
    marginTop: UI.margin * 4,
    marginBottom: UI.margin * 4,
    shadowColor: "black",
    shadowOffset: UI.shadowOffset,
    shadowOpacity: 0.2,
    shadowRadius: UI.borderRadius,
  },
  answersContainer: {
    justifyContent: "center",
    alignItems: "center",
    width: "100%",
  },
  overlay: {
    position: "absolute",
    top: 0,
    width: "100%",
    height: "100%",
    // backgroundColor: "rgba(0, 0, 0, 0.5)",
  },
  button: {
    // width: "100%",
    padding: UI.padding,
    backgroundColor: Colors.warning,
    borderRadius: UI.borderRadius,
  },
  buttonText: {
    color: Colors.primary,
    fontWeight: "bold",
  },
});

export default TestScreen;
