import { Colors } from "@config/Colors";
import { UI } from "@config/constants";
import React from "react";
import { View, Text, StyleSheet, Pressable } from "react-native";

type Props = {
  correctAnswers: number;
  wrongAnswers: number;
  startNewTest: () => void;
};

const TestEndedPopUp: React.FC<Props> = props => {
  const { correctAnswers, wrongAnswers, startNewTest } = props;

  const isFailed = wrongAnswers >= 5;

  const handlePress = () => {
    startNewTest();
  };

  return (
    <View style={styles.component}>
      <View style={styles.card}>
        <View style={styles.header}>
          <Text
            style={[
              styles.text,
              { color: isFailed ? Colors.accent1 : Colors.success },
            ]}>
            {isFailed ? "Ai picat!" : "Ai trecut!"}
          </Text>
        </View>
        <View style={styles.body}>
          <Text style={styles.label}>Corecte: {correctAnswers}</Text>
          <Text style={styles.label}>Gresite: {wrongAnswers}</Text>
        </View>
        <View style={styles.footer}>
          <Pressable style={styles.button} onPress={handlePress}>
            <Text style={styles.buttonText}>
              {isFailed ? "Mai incearca!" : "Incepe un test nou"}
            </Text>
          </Pressable>
        </View>
      </View>
    </View>
  );
};

const styles = StyleSheet.create({
  component: {
    flex: 1,
    justifyContent: "center",
    alignItems: "center",
    padding: UI.padding * 2,
  },
  card: {
    width: "100%",
    // height: "25%",
    padding: UI.padding * 2,
    justifyContent: "center",
    backgroundColor: Colors.secondary,
    borderRadius: UI.borderRadius,
  },
  header: {},
  body: {
    justifyContent: "center",
    width: "100%",
    marginBottom: UI.margin,
  },
  footer: {},
  label: {
    textAlign: "center",
  },
  text: {
    fontSize: 32,
    textAlign: "center",
    fontWeight: "bold",
    marginBottom: UI.margin * 2,
  },
  button: {
    width: "100%",
    padding: UI.padding,
    backgroundColor: Colors.warning,
    borderRadius: UI.borderRadius,
  },
  buttonText: {
    color: Colors.primary,
    fontWeight: "bold",
  },
});

export default TestEndedPopUp;
